@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;0,600;0,700;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  text-align: center;
}

.usersPage {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding-top: 50px;
}

.userList {
  margin-bottom: 50px;
  margin-right: 30px;
  align-content: center;
}

.animalImage {
  border-radius: 15px;
  box-shadow: 7px 7px 13px -6px rgba(0, 0, 0, 0.77);
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.animalImage:hover {
  opacity: 50%;
}

.userList h3 {
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.userList h3:hover {
  color: #3895d3;
}

.userList h4 {
  font-weight: 400;
}

.userList img {
  width: 90%;
}

.spinnerGif {
  width: 100px;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.inputSearch {
  padding: 5px 30px;
  margin: 60px;
  text-align: center;
  outline: none;
  border-radius: 15px;
  border: solid gray 2px;
  transition: 0.2s ease-in-out;
}

.inputSearch:hover {
  border-color: #3895d3;
}

.top-button {
  position: fixed;
  width: 90px;
  top: 90%;
  left: 92%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 99;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.top-button:hover {
  opacity: 80%;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.personalInfo {
  font-weight: 400;
  font-size: 18px;
}

.positionTitle {
  font-weight: 300;
  margin-bottom: 15px;
}

.personalPageImage {
  border-radius: 15px;
  width: 25rem;
  margin-right: 35px;
  box-shadow: 7px 7px 13px -6px rgba(0, 0, 0, 0.77);
}

fieldset {
  width: 90%;
  height: 100%;
  text-align: left;
  padding: 25px;
  margin-right: 15px;
}

.addressField {
  width: 55%;
}

.fieldContainer {
  display: flex;
}

.mainInfoContainer {
  align-items: flex-start;
  display: flex;
  justify-content: center;
  margin-top: 45px;
  margin-bottom: 60px;
}

.friendsList h1 {
  font-weight: 500;
  text-transform: uppercase;
}

.historyContainer {
  display: inline-flex;
  align-items: center;
}

.viewHistory {
  cursor: pointer;
  padding: 5px 10px;
  transition: 0.1s ease-in-out;
}

.viewHistory:hover {
  color: white;
  background-color: black;
}

.errorPage {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.errorMessage {
  font-size: 50px;
}

